.menu {
  background-color: #e8eff9;
  height: 100vh;
}

.menu__top {
  margin-bottom: 40px;
}

.menu__content {
  padding-inline: 20px;
}

.menu__nav {
  margin-bottom: 40px;
}

.menu__phone-number {
  display: block;
  color: inherit;
  text-decoration: none;

  font-size: 16px;
  font-weight: 700;
  line-height: 27px;

  margin-bottom: 16px;
}

.menu__call-to-order {
  display: block;
  width: fit-content;
  color: inherit;
  text-decoration: none;

  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 2px;
  padding-bottom: 8px;
  border-bottom: 1px solid #292929;
}