.header {
  background-image: url(/src/images/header-bg.png);
  background-position: center;
  background-size: cover;
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header__top {
  display: flex;
  justify-content: space-between;
}

.header__top img:first-child {
  height: 16px;
  width: 125px;
}

.header__bottom {
  padding: 0 20px 40px;
}

.header__waves {
  background-image: url(/src/images/sound-waves.png);
  background-size: cover;
  height: 58px;
  width: 48px;
  margin-bottom: 16px;
}

.header__title {
  font-size: 32px;
  font-style: italic;
  font-weight: 800;
  line-height: 32px;
  letter-spacing: -1.5px;
}
